/**
* 统计分析组件
* @module 案件
* @desc 九大案件
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <!-- 图 -->
        <div class="echartswrop">
            选择查询时间段：
            <el-date-picker
                    end-placeholder="结束日期"
                    range-separator="至"
                    start-placeholder="开始日期"
                    type="daterange"
                    v-model="form.dataTime"
            ></el-date-picker>

            <el-button @click="getAccountList" class="btn-css" type="success">查询</el-button>
        </div>
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                border
                class="table-css"
                show-summary
                size="mini"
        >
            <el-table-column align="center" label="执法领域名称" prop="enforceName" show-overflow-tooltip/>
            <el-table-column align="center" label="简易案件数量" prop="normalNumber" show-overflow-tooltip/>
            <el-table-column
                    align="center"
                    label="简易案件金额"
                    prop="normalPenaltyAmount"
                    show-overflow-tooltip
            />
            <el-table-column align="center" label="个人案件数量" prop="personTotal" show-overflow-tooltip/>
            <el-table-column align="center" label="一般案件数量" prop="simpleNumber" show-overflow-tooltip/>
            <el-table-column
                    align="center"
                    label="一般案件金额"
                    prop="simplePenaltyAmount"
                    show-overflow-tooltip
            />
            <el-table-column align="center" label="创建时间" prop="createTime" show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.createTime | formatDate}}</template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
    import {apiStatisticalAnalysisqueryOtherData} from "../../../../api/case";
    import iPagination from "../../../../components/LjPagination";
    import IEcharts from "vue-echarts-v3/src/full.js";
    import {formatDate} from "../../../../until/wordBook";

    export default {
        name: "Ninecases",
        data() {
            return {
                options: [
                    {value: 1, label: "第一季度"},
                    {value: 2, label: "第二季度"},
                    {value: 3, label: "第三季度"},
                    {value: 4, label: "第四季度"},
                    {value: 5, label: "上半年"},
                    {value: 6, label: "下半年"},
                    {value: 7, label: "全年"}
                ],
                optionsType: [
                    {value: 1, label: "简易案件"},
                    {value: 2, label: "一般案件"}
                ],
                bar: {
                    color: ["#3398DB"],
                    title: {
                        text: "检查结果统计不合格率（百分比）"
                    },
                    legend: {
                        data: []
                    },
                    tooltip: {},
                    xAxis: {
                        data: [] //柱状名称
                    },
                    yAxis: {
                        max: 100
                    },
                    series: [
                        {
                            name: "不合格比重（%）",
                            type: "bar",
                            data: [] //柱状数据
                        }
                    ]
                },

                form: {
                    dataTime: null,
                    startTime: "",
                    endTime: ""
                },
                total: 0,
                input: "",
                list: []
            };
        },
        components: {
            IEcharts,
            iPagination
        },
        methods: {

            transTime(data) {
                var date = new Date(data);
                return date.getFullYear();
            },

            //查询+搜索
            getAccountList() {
                this.form.startTime = Date.parse(this.form.dataTime[0]);
                this.form.endTime = Date.parse(this.form.dataTime[1]);
                apiStatisticalAnalysisqueryOtherData(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.otherDateAnalyses;
                });
            }
        },

        filters: {
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd");
            },
            pass(val) {
                return val + "%";
            }
        }
    };
</script>
<style scoped>
    .btn-css {
        margin-left: 10px;
    }

    .echartswrop {
        margin-bottom: 5px;
    }

    .table-css {
        margin-top: 15px;
    }
</style>

