/**
* 统计分析组件
* @module 案件
* @desc 案件对比统计
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <!-- 图 -->
        <div class="echartswrop">
            <el-form :inline="true" :model="form" :rules="rules" class="demo-form-inline" ref="ruleForm">
                <el-form-item label="选择年份：" prop="startTime">
                    <el-date-picker
                            class="formipt-css"
                            format="yyyy"
                            placeholder="选择年份"
                            type="year"
                            v-model="form.startTime"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="对比年份：" prop="endTime">
                    <el-date-picker
                            class="formipt-css"
                            format="yyyy"
                            placeholder="对比年份"
                            type="year"
                            v-model="form.endTime"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="时间：" prop="condition">
                    <el-select class="formipt-css" placeholder="选择时间" v-model="form.condition">
                        <el-option
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                                v-for="item in options"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="选择案件类型：" prop="caseType">
                    <el-select class="formipt-css" placeholder="案件类型" v-model="form.caseType">
                        <el-option
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                                v-for="item in optionsType"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button @click="submitForm('ruleForm')" type="primary">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <!-- 表格1 -->
        <div class="tabletitle">{{title1}}</div>
        <el-table
                :data="firstlist"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                border
                show-summary
                size="mini"
        >
            <el-table-column align="center" label="对比月份" prop="createTime" show-overflow-tooltip/>
            <el-table-column
                    align="center"
                    label="办案部门名称"
                    prop="handlingDepartmentName"
                    show-overflow-tooltip
            />
            <el-table-column align="center" label="简易案件数量" prop="normalNumber" show-overflow-tooltip/>
            <el-table-column align="center" label="单位案件数量" prop="companyTotal" show-overflow-tooltip/>
            <el-table-column
                    align="center"
                    label="简易案件金额"
                    prop="normalPenaltyAmount"
                    show-overflow-tooltip
            />
            <el-table-column align="center" label="个人案件数量" prop="personTotal" show-overflow-tooltip/>
            <el-table-column align="center" label="一般案件数量" prop="simpleNumber" show-overflow-tooltip/>
            <el-table-column
                    align="center"
                    label="一般案件金额"
                    prop="simplePenaltyAmount"
                    show-overflow-tooltip
            />
        </el-table>
        <!-- 表格2 -->
        <div class="tabletitle tabletitle2">{{title2}}</div>
        <el-table
                :data="secendlist"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                border
                show-summary
                size="mini"
        >
            <el-table-column align="center" label="对比月份" prop="createTime" show-overflow-tooltip/>
            <el-table-column
                    align="center"
                    label="办案部门名称"
                    prop="handlingDepartmentName"
                    show-overflow-tooltip
            />
            <el-table-column align="center" label="简易案件数量" prop="normalNumber" show-overflow-tooltip/>
            <el-table-column align="center" label="单位案件数量" prop="companyTotal" show-overflow-tooltip/>
            <el-table-column
                    align="center"
                    label="简易案件金额"
                    prop="normalPenaltyAmount"
                    show-overflow-tooltip
            />
            <el-table-column align="center" label="个人案件数量" prop="personTotal" show-overflow-tooltip/>
            <el-table-column align="center" label="一般案件数量" prop="simpleNumber" show-overflow-tooltip/>
            <el-table-column
                    align="center"
                    label="一般案件金额"
                    prop="simplePenaltyAmount"
                    show-overflow-tooltip
            />
        </el-table>
    </div>
</template>
<script>
    import {apiStatisticalAnalysiscaseCompareAnalysis} from "../../../../api/case";
    import LjPagination from "../../../../components/LjPagination";
    import IEcharts from "vue-echarts-v3/src/full.js";

    export default {
        name: "Casecontrast",
        data() {
            return {
                rules: {
                    startTime: [
                        {required: true, message: "请选择开始年份", trigger: "change"}
                    ],
                    endTime: [
                        {required: true, message: "请选择对比年份", trigger: "change"}
                    ],
                    condition: [
                        {required: true, message: "请选择对比时间段", trigger: "change"}
                    ],
                    caseType: [
                        {required: true, message: "请选择案件类型", trigger: "change"}
                    ]
                },
                options: [
                    {value: 1, label: "第一季度"},
                    {value: 2, label: "第二季度"},
                    {value: 3, label: "第三季度"},
                    {value: 4, label: "第四季度"},
                    {value: 5, label: "上半年"},
                    {value: 6, label: "下半年"},
                    {value: 7, label: "全年"}
                ],
                optionsType: [
                    {value: 1, label: "简易案件"},
                    {value: 2, label: "一般案件"}
                ],
                bar: {
                    color: ["#3398DB"],
                    title: {
                        text: "检查结果统计不合格率（百分比）"
                    },
                    legend: {
                        data: []
                    },
                    tooltip: {},
                    xAxis: {
                        data: [] //柱状名称
                    },
                    yAxis: {
                        max: 100
                    },
                    series: [
                        {
                            name: "不合格比重（%）",
                            type: "bar",
                            data: [] //柱状数据
                        }
                    ]
                },
                title1: "",
                title2: "",
                form: {
                    pageSize: 20,
                    pageNum: 1,
                    startTime: "",
                    endTime: "",
                    caseType: "",
                    condition: ""
                },
                total: 0,
                input: "",
                firstlist: [],
                secendlist: []
            };
        },
        components: {
            IEcharts,
            LjPagination
        },
        methods: {
            //返回年份
            transTime(data) {
                var date = new Date(data);
                return date.getFullYear();
            },

            //提交
            submitForm(formName) {
                this.$refs[formName].validate(valid => {
                    if (valid) {
                        this.getAccountList();
                    } else {
                        this.$message({
                            type: "warning",
                            message: "请把查询条件填写完整!"
                        });
                        return false;
                    }
                });
            },

            //查询+搜索
            getAccountList() {
                let data = {
                    startTime: this.transTime(this.form.startTime),
                    endTime: this.transTime(this.form.endTime),
                    caseType: this.form.caseType,
                    condition: this.form.condition
                };
                apiStatisticalAnalysiscaseCompareAnalysis(data).then(res => {
                    this.firstlist = res.firstDateAnalyses;
                    this.title1 = res.firstDateAnalyses[0].title;
                    this.secendlist = res.secondDateAnalyses;
                    this.title2 = res.secondDateAnalyses[0].title;
                });
            }
        },

        filters: {
            //添加%过滤
            pass(val) {
                return val + "%";
            }
        }
    };
</script>
<style scoped>
    .formipt-css {
        width: 120px;
        margin-right: 10px;
    }

    .tabletitle {
        width: 100%;
        height: 40px;
        text-align: center;
        line-height: 40px;
        font-weight: bold;
    }

    .tabletitle2 {
        margin-top: 40px;
    }

    .echartswrop {
        margin-bottom: 5px;
    }
</style>

