/**
* 统计分析组件
* @module 案件
* @desc 案件汇总
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <!-- 图 -->
        <div class="echartswrop">
            <el-form :inline="true" :model="form" :rules="rules" class="demo-form-inline" ref="ruleForm">
                <el-form-item label=" 选择年份：" prop="year">
                    <el-date-picker
                            class="formipt-css"
                            format="yyyy"
                            placeholder="选择年份"
                            type="year"
                            v-model="form.year"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="月份：" prop="month">
                    <el-select class="formipt-css" placeholder="选择月份" v-model="form.month">
                        <el-option
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                                v-for="item in options"
                        ></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button @click="submitForm('ruleForm')" type="primary">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-tabs :tab-position="tabPosition" class="tab-css">
            <el-tab-pane label="简易案件">
                <el-table
                        :data="easylist"
                        :header-cell-style="{background:'#e9eff8'}"
                        :row-style="{cursor:'pointer'}"
                        border
                        show-summary
                        size="mini"
                >
                    <el-table-column
                            align="center"
                            label="办案部门名称"
                            prop="handlingDepartmentName"
                            show-overflow-tooltip
                    />
                    <el-table-column align="center" label="单位案件数量" prop="companyTotal" show-overflow-tooltip/>
                    <el-table-column align="center" label="简易案件数量" prop="normalNumber" show-overflow-tooltip/>
                    <el-table-column
                            align="center"
                            label="简易案件金额"
                            prop="normalPenaltyAmount"
                            show-overflow-tooltip
                    />
                    <el-table-column align="center" label="个人案件数量" prop="personTotal" show-overflow-tooltip/>
                    <el-table-column align="center" label="一般案件数量" prop="simpleNumber" show-overflow-tooltip/>
                    <el-table-column
                            align="center"
                            label="一般案件金额"
                            prop="simplePenaltyAmount"
                            show-overflow-tooltip
                    />
                    <el-table-column align="center" label="创建时间" prop="createTime" show-overflow-tooltip/>
                </el-table>
            </el-tab-pane>
            <el-tab-pane label="一般案件">
                <el-table
                        :data="normallist"
                        :header-cell-style="{background:'#e9eff8'}"
                        :row-style="{cursor:'pointer'}"
                        border
                        show-summary
                        size="mini"
                >
                    <el-table-column
                            align="center"
                            label="办案部门名称"
                            prop="handlingDepartmentName"
                            show-overflow-tooltip
                    />
                    <!-- <el-table-column align="center" label="办案部门" prop="handlingDepartment" show-overflow-tooltip/> -->
                    <el-table-column align="center" label="单位案件数量" prop="companyTotal" show-overflow-tooltip/>
                    <el-table-column align="center" label="简易案件数量" prop="normalNumber" show-overflow-tooltip/>
                    <el-table-column
                            align="center"
                            label="简易案件金额"
                            prop="normalPenaltyAmount"
                            show-overflow-tooltip
                    />
                    <el-table-column align="center" label="个人案件数量" prop="personTotal" show-overflow-tooltip/>
                    <el-table-column align="center" label="一般案件数量" prop="simpleNumber" show-overflow-tooltip/>
                    <el-table-column
                            align="center"
                            label="一般案件金额"
                            prop="simplePenaltyAmount"
                            show-overflow-tooltip
                    />
                    <el-table-column align="center" label="创建时间" prop="createTime" show-overflow-tooltip/>
                </el-table>
            </el-tab-pane>
        </el-tabs>
        <!-- 表格 -->
    </div>
</template>
<script>
    import {apiStatisticalAnalysisqueryCaseSummary} from "../../../../api/case";
    import IEcharts from "vue-echarts-v3/src/full.js";

    export default {
        name: "Casesummary",
        data() {
            return {
                rules: {
                    year: [{required: true, message: "请选择年份", trigger: "change"}],
                    month: [{required: true, message: "请选择月份", trigger: "change"}]
                },
                tabPosition: "left",
                options: [
                    {value: "01", label: "1月"},
                    {value: "02", label: "2月"},
                    {value: "03", label: "3月"},
                    {value: "04", label: "4月"},
                    {value: "05", label: "5月"},
                    {value: "06", label: "6月"},
                    {value: "07", label: "7月"},
                    {value: "08", label: "8月"},
                    {value: "09", label: "9月"},
                    {value: "10", label: "10月"},
                    {value: "11", label: "11月"},
                    {value: "12", label: "12月"}
                ],
                optionsType: [
                    {value: 1, label: "简易案件"},
                    {value: 2, label: "一般案件"}
                ],
                bar: {
                    color: ["#3398DB"],
                    title: {
                        text: "检查结果统计不合格率（百分比）"
                    },
                    legend: {
                        data: []
                    },
                    tooltip: {},
                    xAxis: {
                        data: [] //柱状名称
                    },
                    yAxis: {
                        max: 100
                    },
                    series: [
                        {
                            name: "不合格比重（%）",
                            type: "bar",
                            data: [] //柱状数据
                        }
                    ]
                },

                form: {
                    year: "",
                    month: ""
                },
                data: {},
                total: 0,
                input: "",
                easylist: [],
                normallist: []
            };
        },
        components: {
            IEcharts
        },
        methods: {
            //返回年份
            transTime(data) {
                var date = new Date(data);
                return date.getFullYear();
            },

            //提交
            submitForm(formName) {
                this.$refs[formName].validate(valid => {
                    if (valid) {
                        this.getAccountList();
                    } else {
                        this.$message({
                            type: "warning",
                            message: "请把查询条件填写完整!"
                        });
                        return false;
                    }
                });
            },

            //查询+搜索
            getAccountList() {
                this.data.year = this.transTime(this.form.year);
                this.data.month = this.form.month;
                apiStatisticalAnalysisqueryCaseSummary(this.data).then(res => {
                    this.easylist = res.simpleCaseSummary;
                    this.normallist = res.normalCaseSummary;
                });
            }
        },

        filters: {
            pass(val) {
                return val + "%";
            }
        }
    };
</script>
<style scoped>
    .echartswrop {
        margin-bottom: 5px;
    }

    .formipt-css {
        width: 120px;
        margin-right: 10px;
    }

    .tab-css {
        min-height: 300px;
        margin-top: 30px;
    }
</style>

